.header {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  border-bottom: 1px solid white;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 72px;
}

.header__box {
  vertical-align: middle;
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-height: inherit;
  /*   position: absolute; */
  width: 1150px;
  height: 72px;
}

.header__logo {
  height: 35px;
  width: auto;
}

.header__menu {
  display: flex;
  align-items: center;
  color: white;
  font-weight: 700;
  height: 100%;
}

.header__menu-items {
  display: flex;
  align-items: center;
  height: 100%;
  padding: 0px 21px;
}

.header__menu li:hover {
  border-bottom: 2px solid white;
  transform: translateY(1px);
}

.header__menu li span {
  margin-right: 8px;
}

.header__btn-box button:first-child {
  border-radius: 25px;
  border: 1.5px solid white;
  background-color: rgba(0, 0, 0, 0);
  color: white;
  width: 90px;
  height: 40px;
  margin-right: 10px;
}

.header__btn-box button:nth-child(2) {
  border-radius: 25px;
  border: 1.5px solid white;
  background-color: white;
  width: 90px;
  height: 40px;
}
